import React from 'react';
import {Link} from 'react-router-dom';

export default class Nav extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            menu: false
        };
        this.toggleMenu = this.toggleMenu.bind(this);
    }
    toggleMenu(){
        this.setState({ menu: !this.state.menu })
    }
    render() {
        window.onscroll = () => {
            var winScroll = document.body.scrollTop || document.documentElement.scrollTop;
            var height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
            var scrolled = (winScroll / height) * 100;
            document.getElementById("progressBar").style.width = scrolled + "%";
        }
        const show = (this.state.menu) ? "show" : "" ;
        return (
            <main className="sticky-top">
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark ps-4">
                    <Link className="navbar-brand" to="/react-app/home">Home</Link>
                    <button className="btn" type="button" onClick={ this.toggleMenu }>
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className={"collapse navbar-collapse " + show}>
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item ms-4">
                                <Link className="nav-link" to="/react-app/comments">Comments</Link>
                            </li>
                            <li className="nav-item ms-4">
                                <Link className="nav-link" to="/react-app/about">About</Link>
                            </li>
                            <li className="nav-item ms-4">
                                <Link className="nav-link" to="/react-app/events">Events</Link>
                            </li>
                        </ul>
                    </div>
                </nav>
                <div className="progress-container">
                    <div id="progressBar"></div>
                </div>
            </main>
        )
    }
}